function sliderCases() {
    var swiper = new Swiper('.cases .mySwiper', {
        slidesPerView: 2,
        spaceBetween: 20,
        navigation: {
            nextEl: ".cases .swiper-button-next",
            prevEl: ".cases .swiper-button-prev",
        },
        pagination: {
            el: ".cases .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            769: {
                slidesPerView: 1.5,
            },
            993: {
                slidesPerView: 2,
            }
        }
    })
}

function sliderCalculations() {
    var swiper = new Swiper('.calculations .mySwiper', {
        slidesPerView: "3",
        spaceBetween: 10,
        navigation: {
            nextEl: ".calculations .swiper-button-next",
            prevEl: ".calculations .swiper-button-prev",
        },
        pagination: {
            el: ".calculations .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: "1.2",
            },
            401: {
                slidesPerView: "1.5",
            },
            451: {
                slidesPerView: "2",
            },
            769: {
                slidesPerView: "3",
            },
            993: {
                slidesPerView: "2",
            },
            1201: {
                slidesPerView: "3",
            }
        }
    })
}

$(document).ready(function () {
    sliderCases();
    sliderCalculations();

    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__wrap').toggleClass('header__wrap--active')
        $('body').toggleClass('fixed-body')
    })

    // Scroll-banner
    var $element = $('.tariffs-banner'),
        section = $(".scroll-banner");

    $(window).scroll(function() {
        var scroll = $(window).scrollTop() + $(window).height();
        var offset = section.offset().top;
        if (scroll > offset) {
            $('.tariffs-banner').addClass("tariffs-banner--static");
        } else {
            $('.tariffs-banner').removeClass("tariffs-banner--static");
        }
    });

    // Select
    $(".custom_select select").niceSelect()

    $('.nice-select .list li').click(function () {
        $(this).parents('.nice-select').addClass('nice-select--active')
    })

    $(".custom_select select").niceSelect()

    $('.data__select-head').click(function () {
        $(this).parent('.select__wrap').toggleClass('select__wrap--active')
    })

    $('.data__select-body li').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".data__select-body li").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.data__select-body li')
    let selectWrap = $('.select__wrap')

    $(document).click(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

    // Accordion
    $(".item-show").click((function(){
        $(this).toggleClass("item-show--active")
        $(this).siblings(".item-hidden").slideToggle()
    }));

    // Map
    $description = $(".description");

    $('.enabled').hover(function() {

        // $(this).attr("class", "enabled heyo");
        $description.addClass('active');
        $description.html($(this).attr('id'));
    }, function() {
        $description.removeClass('active');
    });


    $(document).on('mousemove', function(e){

        $description.css({
            left:  e.pageX,
            top:   e.pageY - 60
        });

    });

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    $(".tab2").click(function () {
        let path = $(this).attr("data-tab2-path")
        $(this).addClass("tab2--active")

        $(this).parents(".tabs-inner2").find(`.tab__content2[data-content2-path="${path}"]`).addClass("tab__content2--active")
    })

    $(".tab3").click(function () {
        let path = $(this).attr("data-tab3-path")
        $(this).toggleClass("tab3--active")

        $(this).parents(".tabs-inner3").find(`.tab__content3[data-content3-path="${path}"]`).toggleClass("tab__content3--active")
    })

    // Delete
    $('.data__subgroup-delete').click(function () {
        let path = $(this).attr("data-delete-btn")
        $(this).parents('.data__subgroups .tab__content2').removeClass('tab__content2--active')

        $(this).parents(".remove-inner").find(`.tab__content2[data-delete-content="${path}"]`).removeClass("tab__content2--active")
    })

    // Copy-text
    $('.promo-code__btn').click(function() {
        var text = $(this).siblings('.promo-code__desc').find('.promo-code__text').text();
        var input = $('<textarea>').val(text).appendTo('body').select();
        document.execCommand('copy');
        input.remove();
    });

    //Quiz-checkbox
    $(".quiz__checkbox").click(function () {
        $(this).parents('.quiz__checkbox-wrap').toggleClass('active')
        $(this).parents('.quiz__checkbox-wrap').siblings(".quiz__checkbox-wrap").removeClass("active")
    })

    $(".quiz__checkboxes-right .quiz__checkbox").click(function () {
        $(this).parents('.quiz__checkboxes').find('.quiz__checkbox-wrap').removeClass('active')
    })

    //edit
    let edit = document.querySelectorAll('.edit-btn');
    let text = document.querySelectorAll('.edit-text');

    for( let i = 0; i < edit.length; i++ ){
        let editMode = false;

        edit[i].addEventListener('click', function(){
            if( editMode ) {
                this.classList.remove("edit-btn--active")
                text[i].removeAttribute('contentEditable');
            } else {
                this.classList.add("edit-btn--active")
                text[i].setAttribute('contentEditable', true);
                text[i].focus();
            }

            editMode = !editMode;
        });
    }

    // Btn-next
    $('.data__btn').click(function () {
        $('.data__wrap-inner').addClass('data__wrap-inner--remove')
        $('.quiz-body').addClass('quiz-body--active')
    })

    // Data - checkbox
    $('.data__checkbox').click(function () {
        $('.data__items2').toggleClass('data__items2--active')
        $('.data__subgroups').toggleClass('data__subgroups--active')
        $('.data__items').toggleClass('data__items--active')
    })

    // Data - input
    $('.data__inner').on("change input", "[data-mirrorto]", function(e, param1) {
        if (param1) {
            return true;
        }
        var t = $(this);
        var to_selector = $(this).data("mirrorto");
        // Записываем значение и дергаем событие
        $(to_selector).val(t.val()).trigger(e.type, true);
    });

    var input = document.querySelectorAll('.data__item2-input input'),
        buffer = [];
    for (var i = 0; input.length > i; i++) {
        console.log(input[i].value);
        buffer[i] = document.createElement('div');
        buffer[i].className = "buffer";
        //вставляем скрытый div.buffer
        input[i].parentNode.insertBefore(buffer[i], input[i].nextSibling);

        input[i].oninput = function() {
            this.nextElementSibling.innerHTML = this.value;
            this.style.width = this.nextElementSibling.clientWidth + 'px';
        };
    }

    //Clone
    let cardConfigTemplate = `
        <div class="checkbox-add__item">
            <div class="checkbox-add__item-inputs">
                <div class="checkbox-add__select custom_select">
                    <select>
                        <option value="Бренд">Бренд</option>
                        <option value="Бренд">Бренд</option>
                        <option value="Бренд">Бренд</option>
                        <option value="Бренд">Бренд</option>
                    </select>
                </div>
                <div class="checkbox-add__item-input">
                    <input type="text" placeholder="% скидки">
                </div>
            </div>
            <button class="checkbox-add__item-btn delete-btn btn btn-blue__outline">
                <svg width="12" height="2" viewBox="0 0 12 2" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" clip-rule="evenodd" d="M0 1.00004C0 0.82323 0.0702379 0.653661 0.195262 0.528636C0.320286 0.403612 0.489856 0.333374 0.666667 0.333374H11.3333C11.5101 0.333374 11.6797 0.403612 11.8047 0.528636C11.9298 0.653661 12 0.82323 12 1.00004C12 1.17685 11.9298 1.34642 11.8047 1.47145C11.6797 1.59647 11.5101 1.66671 11.3333 1.66671H0.666667C0.489856 1.66671 0.320286 1.59647 0.195262 1.47145C0.0702379 1.34642 0 1.17685 0 1.00004Z" fill="#818096"/>
                </svg>
            </button>
         </div>
        `

    $(".btn-add").click(function () {
        $(this).parents('.checkbox-add__items').prepend(cardConfigTemplate)
        $(".custom_select select").niceSelect()
    })

    $(document).on('click', '.delete-btn', function(e) {
        $(this).parents('.checkbox-add__item').remove()
    })

    //Quiz
    $('.quiz-next').click(function (e) {

        let activeStep =$('.quiz-step--active').attr('data-step')

        var $current = $('.quiz-step--active');

        $current.removeClass('quiz-step--active');
        $current.next('.quiz-step').addClass('quiz-step--active');
        if (!$current.next('.quiz-step').get(0)) {
            $('.end').addClass('end--active')
        }

    })

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

})
